@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@1,300&display=swap=);
@font-face {
  font-family: iCielMedium;
  src: local("iCielMedium"), url(/Tesla-Home-Web/static/media/iCiel-Medium.b23bb62d.ttf), format("truetype");
  font-weight: normal;
}
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    Monospace;
}

* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.header {
  display: flex;
  background-color: transparent;
  background: none;
  position: fixed;
  width: 100vw;
  top: 0;
  margin: 15px;
  margin-top: 15px;
  z-index: 100;
}
.header__logo {
  height: 24px;
  width: 100px;
  margin-top: 7px;
}
.header__center {
  flex: 1 1;
  display: flex;
  justify-content: center;
  font-weight: 500;
  font-size: 16px;
}
.header__center p {
  padding: 0 10px 10px 18px;
}
.header__right {
  display: flex;
  justify-content: flex-end;
  text-transform: uppercase;
  margin-right: 21px;
}
.header__right > p {
  margin: 10px;
}

.button {
  background-color: rgba(23, 26, 32, 0.8);
  height: 40px;
  width: 256px;
  margin: 8px;
  display: flex;
  justify-items: center;
  align-items: center;
  border-radius: 100px;
  opacity: 0.85;
}
.button > a {
  text-decoration: none;
  color: white;
  font-size: 12px;
  text-align: center;
}
.button a {
  text-align: center;
  margin-left: 80px;
  font-weight: bold;
}
.button__white {
  background-color: white;
  text-align: center;
  opacity: 0.65;
}
.button__white > a {
  color: black;
}

.item {
  height: 100vh;
  width: 100vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
}
.item__container {
  width: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.item__text {
  margin-top: 20%;
  display: flex;
  flex-direction: column;
  font-size: 34px;
  font-weight: bold;
  padding: 0 0 5px 5px;
  color: #393c41;
  align-items: center;
  flex: 1 1;
  font-family: "Jost", sans-serif;
}
.item__textDesc {
  font-weight: 200;
  font-size: 14px;
}
.item__lowerThird {
  margin-bottom: 5%;
  display: flex;
  flex-direction: column;
}
.item__buttons {
  display: flex;
}
.item__expand {
  color: white;
  width: 100%;
  margin-top: 3%;
  justify-content: center;
  margin-left: 257px;
  animation: icons infinite;
}
@keyframes icons {
  from {
    margin-top: 20px;
  }
  to {
    margin-top: 0px;
  }
}

